<template>
  <div id="main-app" class="container">

    <div class="row mt-4 justify-content-center">
      <div class="col-12 col-md-10 col-lg-8">
        <h2 class="text-left text-muted">{{ title }}</h2>
      </div>

      <search-appointments
        :currFilterKey="filterKey"
        :currFilterDir="filterDir"
        @searchRecords="searchAppointments"
        @requestKey="changeKey"
        @requestDir="changeDir" />

      <add-appointments
        @add="addAppointment"/>

      <appointments-list
        :appointments="filteredApts"
        @removeAppt="removeAppointment"
        @edit="editItem"
        />

    </div>
  </div>
</template>

<script>
  //import { FontAwesomeIcon } from "@fortawesome/vue-fontawesome";
  import axios from "axios";
  import "bootstrap";
  import _ from "lodash";
  import AppointmentsList from "./components/AppointmentsList";
  import SearchAppointments from "./components/SearchAppointments";
  import AddAppointments from "./components/AddAppointments";

  export default {
    data: function() {
      return{
        title: 'Customer Packages',
        appointments:[],
        aptIndex: 0,
        searchTerms: '',
        filterKey: 'petName',
        filterDir: 'asc',

        //package
      }
    },
    components: {
      //FontAwesomeIcon,
      AddAppointments,
      SearchAppointments,
      AppointmentsList
    },
    computed: {
      searchedApts: function(){
        return this.appointments.filter(item => {
          return(
            item.petName.toLowerCase().match(this.searchTerms.toLowerCase()) ||
            item.petOwner.toLowerCase().match(this.searchTerms.toLowerCase())
          );
        });
      },
      filteredApts: function() {
        // use sort feature per lodash library
        return _.orderBy(
          this.searchedApts,
          item => {
            return item[this.filterKey].toLowerCase();
          }, this.filterDir
        );
      }
    },
    methods: {
      addAppointment: function(formData){
        console.log('formData: ', formData);
        formData.aptId = this.aptIndex;
        this.appointments.push(formData);
        this.aptIndex++;
      },
      removeAppointment: function(item){
        // this.appointments.splice(index,1);  // using vanilla js and index arg
        this.appointments = _.without(this.appointments, item);  // using lodash and item arg
      },
      searchAppointments: function(terms){
        this.searchTerms = terms;
      },
      editItem: function(id, field, text){
        const aptIndex = _.findIndex(this.appointments, { aptId: id });
        this.appointments[aptIndex][field] = text;
      },
      changeKey: function(value){
        this.filterKey = value;
      },
      changeDir: function(value){
        this.filterDir = value;
      }
    },
    mounted() {
      // axios.get( "./data/appointments.json" )
      //   .then( response => (
      //       this.appointments = response.data.map(item => {  // for each obj item in response data
      //         item.aptId = this.aptIndex;  // add a key:val pair of { "aptId" : "aptIndex" }
      //         this.aptIndex++;  // increment the aptIndex val to keep it unique
      //         return item;  // return the modified response object and assign to appointments[]
      //       })
      //   ));

      const url = "https://v2aphc77c2.execute-api.us-east-2.amazonaws.com/test/customerpackages"
      axios.get(url)
        .then( (response) => {
          var data = response.data.customerPackagesRows;
          console.log(data);
          var ldict = [];
          for( var row of data ){
            console.log(row);
            var tempobj = {};
            var pkg = 'Pkg: ' + row[2] + ', ' + row[3] + ' of ' + row[4].toString();
            tempobj.petOwner = row[1];  // customer
            tempobj.widget = row[2];
            tempobj.packaging = row[3];
            tempobj.qtyInPkg = row[4];  // qty_in_package
            tempobj.aptDate = '$ ' + row[5].toString();  // price
            tempobj.petName = pkg;  // pkg
            //tempobj.aptNotes = '';app
            ldict.push(tempobj);
          }
          console.log(ldict);
          this.appointments = ldict.map(item => {  // for each obj item in response data
            item.aptId = this.aptIndex;  // add a key:val pair of { "aptId" : "aptIndex" }
            this.aptIndex++;  // increment the aptIndex val to keep it unique
            return item;  // return the modified response object and assign to appointments[]
          })
        });
    }
  };
</script>

<style media="screen">
  body{
    overflow-y: scroll;
  }
</style>
