<template id="">
  <div class="col-12 col-md-10 col-lg-8">
    <div class="input-group my-3">
      <input
        id="SearchApts"
        placeholder="Search on Pkg or Customer"
        type="text"
        class="form-control"
        aria-label="Search Appointments"
        v-model="searchTerm"
      >

      <div class="input-group-append">
        <button
          type="button"
          class="btn btn-outline-info"
          aria-haspopup="true"
          aria-expanded="false"
          @click="clearFilter"
          >
          Clear
        </button>
        <button
          type="button"
          class="btn btn-outline-info dropdown-toggle"
          data-toggle="dropdown"
          aria-haspopup="true"
          aria-expanded="false"
        >
          Sort by
          <span class="caret"></span>
        </button>

        <div class="dropdown-menu dropdown-menu-right">
          <a href="#" class="dropdown-item d-flex justify-content-between" id="petName"
            @click="$emit('requestKey', 'petName')">
            Package Name
            <font-awesome-icon icon="check"
              v-if="currFilterKey==='petName'" />
          </a>

          <!-- <a class="dropdown-item d-flex justify-content-between" href="#" id="aptDate"
            @click="$emit('requestKey', 'aptDate')">
            Price
            <font-awesome-icon icon="check"
              v-if="currFilterKey==='aptDate'" />
          </a> -->

          <a href="#" class="dropdown-item d-flex justify-content-between" id="ownerName"
            @click="$emit('requestKey', 'petOwner')">
            Customer
            <font-awesome-icon icon="check"
              v-if="currFilterKey==='petOwner'" />
          </a>

          <div class="dropdown-divider" role="separator"></div>

          <a
            class="dropdown-item d-flex justify-content-between"
            href="#"
            id="asc"
            @click="$emit('requestDir', 'asc')">
            Asc
            <font-awesome-icon icon="check"
              v-if="currFilterDir==='asc'" />
          </a>

          <a
            class="dropdown-item d-flex justify-content-between"
            href="#"
            id="desc"
            @click="$emit('requestDir', 'desc')">
            Desc
            <font-awesome-icon icon="check"
              v-if="currFilterDir==='desc'" />
          </a>
        </div>
      </div>
    </div>
  </div>

</template>

<script type="text/javascript">
  import { FontAwesomeIcon } from "@fortawesome/vue-fontawesome";

  export default{
    name: 'search-appointments',
    props: [
      'currFilterKey',
      'currFilterDir'
    ],
    data: function(){
      return {
        searchTerm: ''
      };
    },
    watch: {
      searchTerm: function(){
        this.$emit('searchRecords', this.searchTerm);
      }
    },
    methods: {
      clearFilter: function(){
        this.searchTerm = '';
      }
    },
    components: {
      FontAwesomeIcon
    }
  }
</script>

<style media="screen">

</style>
