<template id="">
  <div class="col-12 col-md-10 col-lg-8">
    <div class="card textcenter mt-3">

      <div
        id="addApptBtn" class="card-header bg-info text-white"
        @click="hidePanel=!hidePanel">
        <font-awesome-icon
          icon="plus" class="mr-3"/>Add a Package
      </div>

      <div class="card-body" :class="{ 'd-none': hidePanel }">
        <form id="aptForm"
        @submit.prevent="requestAdd">

          <!-- <div class="form-group form-row">
            <label for="#pet-name">Package Name</label>
            <input
              id="pet-name" class="form-control"
              type="text" name="petName" placeholder="Package Name"
              v-model="formData.petName">
          </div> -->

          <!-- petOwner == customer -->
          <div class="form-group form-row">
            <label for="#pet-owner">Customer</label>
            <input
              id="pet-owner" class="form-control"
              type="text" name="petOwner" placeholder="Customer Name"
              v-model="formData.petOwner">
          </div>

          <!-- widget -->
          <div class="form-group form-row">
            <label for="#widget">Widget</label>
            <input
              id="widget" class="form-control"
              type="text" name="widget" placeholder="Widget"
              v-model="formData.widget">
          </div>

          <!-- packaging -->
          <div class="form-group form-row">
            <label for="#packaging">Packaging</label>
            <input
              id="packaging" class="form-control"
              type="text" name="packaging" placeholder="Packaging"
              v-model="formData.packaging">
          </div>

          <!-- qtyInPkg -->
          <div class="form-group form-row">
            <label for="#qtyInPkg">Qty in Pkg</label>
            <input
              id="qtyInPkg" class="form-control"
              type="text" name="qtyInPkg" placeholder="Qty in Pkg"
              v-model="formData.qtyInPkg">
          </div>

          <!-- aptDate == price -->
          <div class="form-group form-row">
            <label for="#date">Price</label>
            <input
              id="date" class="form-control"
              type="text" name="date" placeholder="0.00"
              v-model="date">
          </div>

          <!-- <div class="form-group form-row">
            <label for="#time">Price</label>
            <input
              id="time" class="form-control"
              type="text" name="time" placeholder=""
              v-model="time">
          </div> -->

          <!-- <div class="form-group form-row">
            <label for="#apt-notes">Appt. Notes</label>
            <textarea
              id="apt-notes" class="w-100 p-2 rounded form-control"
              rows="4" name="aptNotes" placeholder="Appointment Notes"
              v-model="formData.aptNotes"
              ></textarea>
          </div> -->

          <div class="form-group text-right">
            <input
              type="submit" value="Add Appointment"
              class="btn btn-md btn-info mt-4" />
          </div>

        </form>
      </div>

    </div>
  </div>
</template>

<script type="text/javascript">
  import { FontAwesomeIcon } from "@fortawesome/vue-fontawesome";

  export default{
    name: 'add-appointments',
    props: [],
    components: {
      FontAwesomeIcon
    },
    data: function(){
      return {
        hidePanel: true,
        date: '',
        formData: {
          petOwner: '',  // customer
          widget: '',
          packaging: '',
          qtyInPkg: '',
          aptDate: '',  // price
          petName: ''  // pkg
        }
      }
    },
    computed: {
    },
    methods: {
      requestAdd: function(){
        //Pkg: Ant Trap, bag of 10
        var widget = this.formData.widget;
        var packaging = this.formData.packaging;
        var qty = this.formData.qtyInPkg;
        var pkg = 'Pkg: ' + widget + ', ' + packaging + ' of ' + qty;
        this.formData.aptDate = this.date;
        this.formData.petName = pkg;
        this.$emit('add', this.formData);
        this.formData = {};
        this.hidePanel = true;
      }
    }
  }
</script>

<style media="screen">
  #addApptBtn:hover{
    cursor: pointer;
  }
</style>
