// import vue and main app component
import Vue from "vue";
import App from "./App.vue";

// import dependency libraries
import "bootstrap";  // bootstrap js
import "bootstrap/dist/css/bootstrap.css";

// import Font Awesome icons
import {library} from "@fortawesome/fontawesome-svg-core";  // import fontawesome
import{ faTrash, faPlus, faMinus, faCheck, faInfoCircle } from "@fortawesome/free-solid-svg-icons";  // import icons you want
library.add( faTrash, faPlus, faMinus, faCheck, faInfoCircle );  // add icons to library

// create and mount instance of Vue with App component
Vue.config.productionTip = false

new Vue({
  render: h => h(App)
}).$mount("#app");
