text-info<template id="">
  <div class="col-12 col-md-10 col-lg-8">
    <div class="list-group list-group-flush">
      <div class="">
        <!-- Flex wrapper and list-group style -->
        <div
          class="list-group-item d-flex align-items-start"
          v-for="item in appointments"
          :key="item.aptId">
          <button
            class="mr-2 btn btn-sm btn-outline-danger"
            @click="$emit('removeAppt', item)">
            <font-awesome-icon icon="trash"></font-awesome-icon></button>
          <div class="w-100">
            <div class="d-flex justify-content-between">
              <span class="h4 text-info p-1">{{ item.petName }}</span>
              <span class="float-right text-info"><b>{{ (item.aptDate) }}</b></span>
            </div>
            <div class="mb-1 mt-1">
              <a href="#" title="Contact Info">
                <span class="text-muted mr-2">
                  <font-awesome-icon icon="info-circle"></font-awesome-icon>
                </span>
              </a>
              <span class="font-weight-bold text-info mr-1">Customer:</span>
              <span class="p-1">{{ item.petOwner }}</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script type="text/javascript">
  import { FontAwesomeIcon } from "@fortawesome/vue-fontawesome";
  import moment from "moment";

  export default{
    name: 'appointments-list',
    props: ['appointments'],
    components: {
      FontAwesomeIcon
    },
    methods: {
      formattedDate: function(date){
        return moment(new Date(date)).format('MM/DD/YY, h:mm A');
      }
    }
  }
</script>

<style media="screen">
  [contenteditable="true"]:focus{
    background: rgba(255, 255, 225, 0.9);
    outline: 1px solid rgb(190, 220, 235);
    outline-radius: 10px;

    -webkit-box-shadow: 0px 0px 1px 4px rgba(193,221,235,0.9);
    -moz-box-shadow: 0px 0px 1px 4px rgba(193,221,235,0.9);
    box-shadow: 0px 0px 1px 4px rgba(193,221,235,0.9);
  }
</style>
